<template>
  <div class="category">
    <!-- 分类 -->
    <van-nav-bar title="全部分类" fixed />

    <!-- 搜索框 -->
    <SearchBanner></SearchBanner>

    <!-- 分类列表 -->
    <div class="list-box">
      <div class="left" ref="leftContainer">
        <ul>
          <li v-for="(item, index) in list" :key="item.category_id">
            <a :class="{ active: index === activeIndex }" @click="scrollToTop(index)" href="javascript:;">{{ item.name
            }}</a>
          </li>
        </ul>
      </div>

      <div class="right">
        <ul class="right-container" ref="rightContainer">
          <li @click="toSearch(item.category_id, item.name)" v-for="item in list[activeIndex]?.children"
            :key="item.category_id" class="cate-goods">
            <img :src="item.image?.external_url" alt="" />
            <p>{{ item.name }}</p>
          </li>
          <li @click="toSearch(item.category_id, item.name)" v-for="item in list[activeIndex]?.children"
            :key="item.category_id + 10000" class="cate-goods">
            <img :src="item.image?.external_url" alt="" />
            <p>{{ item.name }}</p>
          </li>
        </ul>

      </div>
    </div>
  </div>
</template>

<script>
import SearchBanner from '@/components/SearchBanner'

import { getCategoryDataApi } from '@/api/category'

export default {
  name: 'CategoryPage',

  components: {
    SearchBanner
  },

  created () {
    console.log('created触发')
    this.getCategoryList()
  },

  activated () {
    console.log('activated触发')
    // this.$refs.leftContainer.scroll({ left: 0, top: this.activeIndex * 45, behavior: 'smooth' })
    this.$refs.leftContainer.scrollTop = this.activeIndex * 45
    // this.$refs.rightContainer.scroll({ top: this.rightContainerScrollTop, left: 0 })
    this.$refs.rightContainer.scrollTop = this.rightContainerScrollTop
  },

  beforeRouteLeave (to, from, next) {
    this.rightContainerScrollTop = this.$refs.rightContainer.scrollTop
    // console.log(this.$refs.rightContainer.scrollTop)
    next()
  },

  data () {
    return {
      list: [],
      activeIndex: 0,
      rightContainerScrollTop: 0
    }
  },

  computed: {
  },

  methods: {
    async getCategoryList () {
      const res = await getCategoryDataApi()
      this.list = res.data.list
    },

    toSearch (id, name) {
      this.$router.push(`/searchresult?catgryId=${id}&catgryName=${name}`)
    },

    scrollToTop (index) {
      this.activeIndex = index
      // console.log(this.$refs.leftContainer.children[this.activeIndex])
      // this.$refs.leftContainer.children[this.activeIndex].scrollIntoView({
      //   behavior: 'smooth',
      //   block: 'start'
      // })
      this.$refs.rightContainer.scroll({ top: 0, left: 0 })
      this.$refs.leftContainer.scroll({ left: 0, top: this.activeIndex * 45, behavior: 'smooth' })
    }
  }
}
</script>

<style lang="less" scoped>
// 主题 padding
.category {
  padding-top: 100px;
  padding-bottom: 50px;
  height: 100vh;

  .list-box {
    height: 100%;
    display: flex;

    .left {
      width: 85px;
      height: 100%;
      background-color: #f3f3f3;
      overflow: auto;

      a {
        display: block;
        height: 45px;
        line-height: 45px;
        text-align: center;
        color: #444444;
        font-size: 12px;

        &.active {
          color: #fb442f;
          background-color: #fff;
        }
      }
    }

    .right {
      flex: 1;
      // padding: 10px 0;

      .right-container {
        height: 100%;
        background-color: #ffffff;
        display: flex;
        flex-wrap: wrap;
        justify-content: flex-start;
        align-content: flex-start;
        overflow: auto;
      }

      .cate-goods {
        width: 33.3%;
        margin-bottom: 10px;

        img {
          width: 70px;
          height: 70px;
          display: block;
          margin: 5px auto;
        }

        p {
          text-align: center;
          font-size: 12px;
        }
      }
    }
  }
}

// 导航条样式定制
.van-nav-bar {
  z-index: 999;
}

// 搜索框样式定制
.van-search {
  position: fixed;
  width: 100%;
  top: 46px;
  z-index: 999;
}
</style>
